import { Api } from '../src/components/base/api';
import { AppState } from './AppState';
import { EventEmitter } from './components/base/events';
import './scss/styles.scss';
import { API_URL, CDN_URL } from './utils/constants';
import { cloneTemplate, ensureElement } from './utils/utils';
import { Card, Modal, Order, Success, View, ViewBasket } from './view';
const events = new EventEmitter();
class getData extends Api {
    constructor(baseUrl, imageLink, options) {
        super(baseUrl, options);
        this.imageLink = imageLink;
    }
    getData() {
        return this.get('/product').then((data) => data.items.map((item) => (Object.assign(Object.assign({}, item), { image: this.imageLink + item.image }))));
    }
    getOrder(order) {
        return this.post('/order', order).then((data) => data);
    }
}
const getDataApi = new getData(API_URL, CDN_URL);
const app = new AppState({}, events);
const view = new View(document.body, events);
const basketTemplate = document.querySelector('#basket');
const orderTemplate = document.querySelector('#order');
const contactsTemplate = document.querySelector('#contacts');
const cardCatalogTemplate = document.querySelector('#card-catalog');
const cardPreviewTemplate = document.querySelector('#card-preview');
const cardBasketTemplate = document.querySelector('#card-basket');
const successTemplate = document.querySelector('#success');
const modal = new Modal(ensureElement('#modal-container'), events);
const basket = new ViewBasket(cloneTemplate(basketTemplate), events);
const order = new Order(cloneTemplate(orderTemplate), events);
const contacts = new Order(cloneTemplate(contactsTemplate), events);
getDataApi
    .getData().then(app.catalogInit.bind(app)).catch((err) => {
    console.error(err);
});
events.onAll(({ eventName, data }) => {
    console.log(eventName, data);
});
events.on('items:changed', () => {
    view.catalog = app.catalog.map((item) => {
        const card = new Card(cloneTemplate(cardCatalogTemplate), {
            onClick: () => events.emit('card:select', item),
        });
        return card.render({
            category: item.category,
            title: item.title,
            image: item.image,
            price: item.price,
        });
    });
});
events.on('card:select', (item) => app.previewInit(item));
events.on('preview:changed', (item) => {
    const card = new Card(cloneTemplate(cardPreviewTemplate), {
        onClick: () => {
            events.emit('item:check', item);
            card.button = app.basket.indexOf(item) !== -1 ? 'Удалить из корзины' : 'Добавить в корзину';
        }
    });
    modal.render({
        content: card.render({
            category: item.category,
            title: item.title,
            image: item.image,
            description: item.description,
            button: app.basket.indexOf(item) !== -1 ? 'Удалить из корзины' : 'Добавить в корзину',
            price: item.price
        })
    });
});
events.on('modal:open', () => {
    view.locked = true;
});
events.on('modal:close', () => {
    view.locked = false;
});
events.on('item:check', (item) => {
    app.basket.indexOf(item) !== -1
        ? events.emit('item:remove', item)
        : events.emit('item:add', item);
});
events.on('item:add', (item) => {
    app.add(item);
});
events.on('item:remove', (item) => {
    app.remove(item);
});
events.on('count:changed', () => (view.counter = app.basket.length));
events.on('basket:changed', (items) => {
    basket.items = items.map((item, basketId) => {
        const card = new Card(cloneTemplate(cardBasketTemplate), {
            onClick: () => {
                events.emit('item:remove', item);
            }
        });
        return card.render({
            basketId: (basketId + 1).toString(),
            title: item.title,
            price: item.price
        });
    });
    const total = items.reduce((acc, item) => acc + item.price, 0);
    basket.total = total;
    app.order.total = total;
});
events.on('basket:open', () => {
    basket.selected = app.basket;
    modal.render({
        content: basket.render({})
    });
});
events.on('order:open', () => {
    app.order.items = app.basket.map((item) => item.id);
    modal.render({
        content: order.render({
            payment: '',
            address: '',
            valid: false,
            errors: []
        })
    });
});
events.on('order:change', ({ name }) => {
    app.order.payment = name;
    app.validate();
});
events.on('order:submit', () => {
    modal.render({
        content: contacts.render({
            email: '',
            phone: '',
            valid: false,
            errors: []
        })
    });
});
events.on('formErrors:change', (errors) => {
    const { payment, address, email, phone } = errors;
    order.valid = !payment && !address;
    order.errors = Object.values({ payment, address }).filter((err) => !!err).join('; ');
    contacts.valid = !email && !phone;
    contacts.errors = Object.values({ phone, email }).filter((err) => !!err).join('; ');
});
events.on(/^order\..*:change/, (data) => {
    app.orderInit(data.field, data.value);
});
events.on(/^contacts\..*:change/, (data) => {
    app.orderInit(data.field, data.value);
});
events.on('contacts:submit', () => {
    getDataApi.getOrder(app.order).then(() => {
        const success = new Success(cloneTemplate(successTemplate), {
            onClick: () => {
                modal.close();
            }
        });
        success.total = `Стоимость заказа: ${app.order.total} синапсов`;
        app.clear();
        modal.render({
            content: success.render({})
        });
    }).catch((err) => {
        console.error(err);
    });
});
