class Model {
    constructor(data, events) {
        this.events = events;
        Object.assign(this, data);
    }
    changes(event, loading) {
        this.events.emit(event, loading !== null && loading !== void 0 ? loading : {});
    }
}
export class AppState extends Model {
    constructor() {
        super(...arguments);
        this.basket = [];
        this.order = {
            payment: '',
            address: '',
            email: '',
            phone: '',
            total: 0,
            items: []
        };
        this.formErrors = {};
    }
    add(card) {
        if (card.price !== null && this.basket.indexOf(card) === -1) {
            this.basket.push(card);
            this.changes('count:changed', this.basket);
            this.changes('basket:changed', this.basket);
        }
    }
    remove(card) {
        this.basket = this.basket.filter(item => item !== card);
        this.changes('count:changed', this.basket);
        this.changes('basket:changed', this.basket);
    }
    clear() {
        this.basket = [];
        this.changes('count:changed', this.basket);
        this.changes('basket:changed', this.basket);
    }
    total() {
        return this.basket.reduce((acc, item) => acc + item.price, 0);
    }
    validate() {
        const errors = {};
        if (!this.order.payment) {
            errors.payment = 'Payment type is required';
        }
        if (!this.order.address) {
            errors.address = 'Address is required';
        }
        if (!this.order.email) {
            errors.email = 'Email is required';
        }
        else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(this.order.email)) {
            errors.email = 'Email is not valid';
        }
        if (!this.order.phone) {
            errors.phone = 'Phone is required';
        }
        else if (!/^(\+7|8)\s?\(?\d{3}\)?\s?\d{3}\s?\d{2}\s?\d{2}$/.test(this.order.phone)) {
            errors.phone = 'Phone is not valid';
        }
        this.formErrors = errors;
        this.events.emit('formErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    catalogInit(data) {
        this.catalog = data;
        this.changes('items:changed', { catalog: this.catalog });
    }
    previewInit(data) {
        this.preview = data.id;
        this.changes('preview:changed', data);
    }
    orderInit(field, value) {
        if (field === 'total') {
            this.order[field] = value;
        }
        else if (field === 'items') {
            this.order[field].push(value);
        }
        else {
            this.order[field] = value;
        }
        if (this.validate()) {
            this.events.emit('order:ready', this.order);
        }
    }
}
